/** @jsxImportSource @emotion/react */
import { css, ThemeProvider } from "@emotion/react";
import { useSelector, useDispatch } from "react-redux";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Title from "@components/Title";
import Nav from "@components/Nav";
import Timer from "@components/Timer";
import Stopwatch from "@components/Stopwatch";
import Clock from "@components/Clock";
import Dday from "@components/Dday";
import Header from "@components/Header";
import { RootState } from "@src/store";
import useTimer from "@hooks/useTimer";
import "@src/App.css";
import useStopwatch from "@hooks/useStopwatch";

import colorSet from "@styles/palette";
import { useLayoutEffect } from "react";

//  themeState    theme 

const App = () => {
  const { tap } = useSelector((state: RootState) => state.tapReducer); // tapReducer    . (  )
  const { themeStatus } = useSelector(
    (state: RootState) => state.optionReducer
  );
  //        

  const dispatch = useDispatch();

  const timerProps = useTimer();
  const stopwatchProps = useStopwatch();

  const timerSecond = timerProps.time.second;
  const timerMinute = timerProps.time.minute;
  const timerHour = timerProps.time.hour;
  const timerStatus = timerProps.status;

  //    
  //           dependency   
  //      paint      useLayoutEffect 
  useLayoutEffect(() => {
    const savedTheme = localStorage.getItem("themeState");
    if (savedTheme === "dark") dispatch({ type: "dark" });
  }, []);

  return (
    <ThemeProvider
      theme={themeStatus == "dark" ? colorSet.dark : colorSet.light}
    >
      <BrowserRouter basename="/SsocoTimer">
        <div
          css={Viewport({
            themeStatus,
            timerSecond,
            timerMinute,
            timerHour,
            timerStatus,
          })}
        >
          <div css={PageContainer}>
            <Nav />
            <div css={MainContainer}>
              <Header />
              <Title />
              <Routes>
                {tap === "Timer" && (
                  <Route
                    path="/*"
                    element={<Timer props={timerProps} />}
                  ></Route>
                )}
                {tap === "Stopwatch" && (
                  <Route
                    path="/stopwatch"
                    element={<Stopwatch props={stopwatchProps} />}
                  ></Route>
                )}
                {tap === "Clock" && (
                  <Route path="/clock" element={<Clock />}></Route>
                )}
                {tap === "Dday" && (
                  <Route path="/dday" element={<Dday />}></Route>
                )}
              </Routes>
            </div>
          </div>
        </div>
      </BrowserRouter>
    </ThemeProvider>
  );
};

interface ViewportProps {
  themeStatus: string;
  timerSecond: number;
  timerMinute: number;
  timerHour: number;
  timerStatus: string;
}

const Viewport = (props: ViewportProps) => css`
  width: 100vw;
  height: 100vh;
  min-width: 500px;
  min-height: 720px;
  background-color: ${props.themeStatus === "light"
    ? props.timerStatus === "play" &&
      props.timerHour === 0 &&
      props.timerMinute === 0 &&
      props.timerSecond === 2
      ? colorSet.light.alert1
      : props.timerStatus === "play" &&
        props.timerHour === 0 &&
        props.timerMinute === 0 &&
        props.timerSecond === 1
      ? colorSet.light.alert2
      : props.timerStatus === "play" &&
        props.timerHour === 0 &&
        props.timerMinute === 0 &&
        props.timerSecond === 0
      ? colorSet.light.alert3
      : colorSet.light.bgPage1
    : props.timerStatus === "play" &&
      props.timerHour === 0 &&
      props.timerMinute === 0 &&
      props.timerSecond === 2
    ? colorSet.dark.alert1
    : props.timerStatus === "play" &&
      props.timerHour === 0 &&
      props.timerMinute === 0 &&
      props.timerSecond === 1
    ? colorSet.dark.alert2
    : props.timerStatus === "play" &&
      props.timerHour === 0 &&
      props.timerMinute === 0 &&
      props.timerSecond === 0
    ? colorSet.dark.alert3
    : colorSet.dark.bgPage1};
  color: ${props.themeStatus === "light"
    ? colorSet.light.text1
    : colorSet.dark.text1};
  transition: 0.125s all ease-in;
`;

const PageContainer = css`
  position: absolute;
  left: 50%;
  top: 10%;
  transform: translate(-50%);
  display: flex;
`;

const MainContainer = css`
  width: 500px;
`;

export default App;
