import { useState, useRef, useEffect } from "react";
import useTimeManage from "./utils/useTimeManage";

interface LapState {
  hour: number;
  minute: number;
  second: number;
  ms: number;
}

const useStopwatch = () => {
  const { time, setTime, playTimeout } = useTimeManage();
  const { ms, second, minute, hour } = time;
  const [lap, setLap] = useState<LapState[]>([]); // 
  const [status, setStatus] = useState<string>("stop");
  const startTime = useRef<number | null>(null); //    ref
  const pauseTime = useRef<number | null>(null); //     ref
  const lapDOM = useRef<HTMLDivElement | null>(null); // lapDOM  ref 

  //    

  const timePlay = () => {
    //  
    if (status === "stop") startTime.current = Date.now();
    // pause 
    else if (status === "pause") {
      if (startTime.current && pauseTime.current)
        startTime.current += Date.now() - pauseTime.current;
    }
    setStatus("play");
  };

  const timeLap = () => {
    if (status === "play")
      setLap((prev) => [...prev, { hour, minute, second, ms }]);
  };

  const downScroll = () => {
    const refDOM = lapDOM.current;
    if (!refDOM) return;
    refDOM.scrollTop = refDOM.scrollHeight;
  };

  const timePause = () => {
    if (status === "play") {
      if (playTimeout.current) clearInterval(playTimeout.current);
      pauseTime.current = Date.now();
    }
    setStatus("pause");
  };

  const timeReset = () => {
    setStatus("stop");
    if (playTimeout.current) clearInterval(playTimeout.current);
    setLap([]);
    setTime({ ...time, ms: 0, second: 0, minute: 0, hour: 0 });
    startTime.current = null;
    pauseTime.current = null;
  };

  // lap     
  useEffect(() => {
    downScroll();
  }, [lap]);

  useEffect(() => {
    // play   
    if (!startTime.current) startTime.current = Date.now();
    if (status === "play") {
      const now = new Date(Date.now() - startTime.current);
      playTimeout.current = setTimeout(() => {
        setTime({
          ...time,
          ms: now.getUTCMilliseconds(),
          second: now.getUTCSeconds(),
          minute: now.getUTCMinutes(),
          hour: now.getUTCHours(),
        });
      }, 1);
    }
    // clean-up    "state  ->  ->  ->   "   useEffect   !
    return () => {
      if (playTimeout.current) clearTimeout(playTimeout.current);
    };
  }, [playTimeout, setTime, time, status]);

  return {
    time,
    lap,
    status,
    lapDOM,
    timePlay,
    timeLap,
    timePause,
    timeReset,
  };
};

export default useStopwatch;
